@import url(https://fonts.googleapis.com/css?family=Roboto);
@import url(https://fonts.googleapis.com/css2?family=Arvo:wght@700&display=swap);
body {
  font-family: "Roboto", sans-serif;
  background-color: rgb(99, 212, 174);
}


.TodoList {
  color: black;
  background-color: white;
  border-radius: 10px;
  text-align: center;
  margin: 50px auto;
  padding: 25px 50px;
  width: 600px;
}

.TodoList ul {
  list-style: none;
  margin-top: 1rem;
}

.TodoList h1 {
  font-family: "Arvo", serif;
  font-weight: normal;
  font-size: 2.6rem;
  border-bottom: 1px solid silver;
}

.Todo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(255, 255, 255, 0.1);
  margin-left: -28px;
}

.Todo-Task {
  cursor: pointer;
  position: relative;
  transition: all 0.4s linear;
  font-size: 21px;
}

.Todo .completed {
  opacity: 0.6;
}
.Todo .completed:before {
  content: "";
  position: absolute;
  top: 50%;
  left: -20px;
  display: block;
  width: 100%;
  height: 2px;
  background-color: #008984;
  -webkit-animation: strikeitem 0.4 ease-out 0.4s forwards;
          animation: strikeitem 0.4 ease-out 0.4s forwards;
}

@-webkit-keyframes strikeitem {
  to {
    width: calc(100% + 80px);
  }
}

@keyframes strikeitem {
  to {
    width: calc(100% + 80px);
  }
}

.Todo-buttons {
  flex-shrink: 0;
  padding: 5px;
}

.Todo-buttons button {
  border: none;
  font-size: 16px;
  background-color: #fff;
  -webkit-appearance: none;
  cursor: pointer;
  color: #053037;
  margin: 6px;
}

.Todo-edit {
  display: flex;
}

.Todo-edit input {
  padding: 10px 4px;
  border: none;
  font-size: 21px;
  border: 1px solid rgb(179, 172, 172);
  outline: none;
  transition: all 0.4s ease-in;
  width: 480px;
}

.Todo-edit input:hover {
  border: 1px solid black;
}

.Todo-edit button {
  height: 100%;
  padding: 0px 18px;
  background: #008984;
  color: white;
  text-transform: uppercase;
  font-weight: bold;
  border: 1px solid rgba(255, 255, 255, 0.3);
  cursor: pointer;
  transition: 0.2s ease-out;
  transition: all 0.4s ease-in-out;
}

.Todo-edit button:hover {
  background-color: white;
  border: 1px solid #008984;
  color: #008984;
}

.editBtn {
  width: 21px;
  height: 21px;
}

.trashBtn {
  width: 22px;
  height: 22px;
}

.NewTodo {
  display: flex;
  margin-top: 16px;
}

.NewTodo input {
  margin-top: 10px;
  flex-grow: 1;
  border: none;
  padding: 10px 7px;
  font-size: 21px;
  border: 1px solid rgb(179, 172, 172);
  outline: none;
  transition: all 0.4s ease-in;
  margin-bottom: 11px;
}

.NewTodo input:hover {
  border: 1px solid black;
}

.NewTodo button {
  margin-top: 10px;
  padding: 0px 20px;
  border: none;
  background: #008984;
  color: white;
  text-transform: uppercase;
  font-weight: bold;
  border: 1px solid rgba(255, 255, 255, 0.3);
  cursor: pointer;
  transition: 0.2s ease-out;
  margin-bottom: 11px;
  transition: all 0.4s ease-in-out;
}

.NewTodo button:hover {
  background-color: white;
  border: 1px solid #008984;
  color: #008984;
}

